import React, { Component, Fragment } from "react";
import { Form, Input, Button } from "antd";
import { connect } from "react-redux";
import { otp } from "../../actions/auth";
import PropTypes from "prop-types";
import { updateTitle } from "../common/UpdateTitle";
class Otp extends Component {
  static propTypes = {
    otp: PropTypes.func.isRequired,
    isActive: PropTypes.bool,
  };
  onSubmit = (e) => {
    this.props.otp(e.otp, this.props.user.id);
    console.log(this.props.user.id);
  };
  componentDidMount() {
    updateTitle({
      title: "Confirm Your OTP",
    });
  }
  render() {
    return (
      <Fragment>
        <div className="page-section pb-0">
          <div className="container page__container d-flex flex-column flex-sm-row align-items-sm-center pt-sm-64pt pb-32pt pt-32pt">
            <div className="flex">
              <h1 className="h2 mb-0">Account activation</h1>
              <p className="text-breadcrumb">OTP verification</p>
            </div>
            <p className="d-sm-none"></p>
            <a href="/" className="btn btn-outline-secondary flex-column">
              Need Help?
              <span className="btn__secondary-text">Contact us</span>
            </a>
          </div>
        </div>

        <div className="page-section">
          <div className="container page__container">
            <div className="page-separator">
              <div className="page-separator__text">Confirm your OTP</div>
            </div>
            <div className="col-sm-6 p-0">
              <Form
                onFinish={this.onSubmit}
                className="mx-auto"
                name="otp_form"
              >
                <Form.Item className="form-group">
                  <label className="form-label">OTP:</label>
                  <Form.Item
                    name="otp"
                    rules={[
                      {
                        required: true,
                        message: "Please input OTP",
                      },
                    ]}
                    noStyle
                  >
                    <Input type="number" className="form-control" />
                  </Form.Item>

                  <small className="form-text text-muted">
                    A message with OTP has been sent to your phone number.
                  </small>
                </Form.Item>
                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="btn btn-primary"
                  >
                    Confirm
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.auth.user,
  isActive: state.auth.isActive,
  error: state.errors,
});

export default connect(mapStateToProps, { otp })(Otp);
